export default {
	props: {
		currentPage: {
			type: String,
			default: 'index'
		}
	},
	//行内重置掉中间功能按钮的样式
	computed: {
		style() {
			let style = `width:${30}%; 
					background:radial-gradient( at top,transparent 50%,#fff 0%); 
					`;
			// if (this.bgImage) {
			// 	style = `${style}background-image:url(${bgImage});`;
			// }
			return style;
		},
		//扫码大图标的样式
		IMGstyle() {
			let IMGstyle =
				`width:${140}rpx;
						height:${140}rpx;
						position: absolute;
						left: 0; 
						right: 0; 
						margin-left: auto; 
						margin-right: auto;
						bottom : ${30}rpx;
						max-width: 50%;
						text-align: center;
						`;
			// if (this.bgImage) {
			// 	style = `${style}background-image:url(${bgImage});`;
			// }
			return IMGstyle;
		}
	},
	data() {
		return {
			isFullSucreen: this.isFullSucreen,
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			tabBar: [{
					type: 'jump',
					url: 'index',
					text: '首页',
					imgNormal: '/static/n1.png',
					imgClick: '/static/s1.png'
				},
				{
					type: 'jump',
					url: 'classify',
					text: '分类',
					imgNormal: '/static/n2.png',
					imgClick: '/static/s2.png'
				},
				{
					type: 'jump',
					url: 'news',
					text: '资讯',
					imgNormal: '/static/n3.png',
					imgClick: '/static/s3.png'
				},
				{
					type: 'jump',
					url: 'cart',
					text: '购物车',
					imgNormal: '/static/n4.png',
					imgClick: '/static/s4.png'
				},{
					type: 'jump',
					url: 'user',
					text: '我的',
					imgNormal: '/static/n5.png',
					imgClick: '/static/s5.png'
				}
			]
		};
	},
	created() {
		uni.hideTabBar({}); 
	},
	methods: {
		navTo(item) {
			let _this = this;
			switch (item.type) {
				//在未登录的时候 点击我的 引导到登录页
				case 'jump':
					if (item.url !== this.currentPage) {
						var isUrl = `/pages/${item.url}/${item.url}`;
						uni.switchTab({
							url: isUrl
						});
					} else {
						// this.$api.toTop();
					}
					break;
				case 'scanning':
					// 只允许通过相机扫码
					uni.scanCode({
						onlyFromCamera: true,
						success: function(res) {
							console.log(res);

							if (res.scanType == 'WX_CODE') {
								// let centerData = unescape(res.path).split(',')[1];
								// let machineId = centerData.split(':')[1]
								// console.log(machineId);
								//2020 02 18 ==== 二维码改格式了  直接用path跳转传参
								let path = `/` + res.path + '&isBack=true';
								console.log('path');
								console.log(path);
								//携参跳转
								if (path !== 0) {
									//判断是否登录 是直接开始业务 不是的话走登录
									if (_this.loginStatus == null) {
										uni.navigateTo({
											url: '/pages/NotLogIn/NotLogIn?url=' + escape(path) //=== >>> URL编码 解码方法 unescape()
										});
									} else {
										uni.navigateTo({
											url: path
										});
									}
								} else {
									_this.$api.msg({
										title: '八成是这个二维码有问题.'
									});
								}
							} else {
								_this.$api.msg({
									title: '您扫的是啥码啊?'
								});
							}
						}
					});

					// 这里因为暂时没有示例二维码 ； 所以直接跳转到业务开始页面 ；  正常应该是扫码成功后跳转
					// uni.navigateTo({
					// 	//假定为1001
					// 	url: '/pages/carWash/ConfirmParking?machineId=1001'
					// });
					break;
				default:
					console.log('没有可执行命令');
					break;
			}
		}
	}
};
